const ADD_TO_CART = 'ADD_TO_CART'

const increment = createAction(ADD_TO_CART)

const cartReducer = (state, action) => {
    if (typeof state === 'undefined') {
        return 0
    }

    switch (action.type) {
        case 'INCREMENT':
            return state + 1
        case 'DECREMENT':
            return state - 1
        default:
            return state
    }
}

